// ResumeDataItem.js

export class ResumeDataItem {
    constructor() {
      this.frontend_id = "";
      this.id = ""; //  original data id
      this.type = ""; // "summary" | "work_exp" | "proj_exp"
      this.saved = false;
      this.shown = true;
      this.currentActivelabel = 0;
      this.locked = false;
  
      this.meta = {
        project_name: "",
        company_name: "",
        is_customized_company_name: true,
        customized_company_name: "",
        time_start: "", //  "2025-05-26"
        time_end: "",
        on_going: false,
        role: ""
      };
  
      this.data = [
        {
          id: "",
          label: 0, // 0: ori, 1: v1, 2: v2, 3: v3
          item_desc: "",
          resp_desc: "",
          achi_desc: "",
          created_time: "", //  "2025-05-26T12:00:00Z"
          key_words: "" // JSON 
        }
      ];
    }

    static isEqual(a, b) {
      if (!(a instanceof ResumeDataItem) || !(b instanceof ResumeDataItem)) return false;
      return (
        a.id === b.id &&
        a.type === b.type &&
        a.saved === b.saved &&
        JSON.stringify(a.meta) === JSON.stringify(b.meta) &&
        JSON.stringify(a.data) === JSON.stringify(b.data)
      );
    }
}
  